'use client'
import React from 'react'
import HeaderProductos from '../../components/productos/HeaderProductos'
import FiltrosProductos from '../../components/productos/FiltrosProductos'
import TablaProductos from '../../components/productos/TablaProductos'
import FormularioNuevoProductoModal from '../../components/productos/FormularioNuevoProductoModal'
import '@/styles/productos.css'

export default function GestionDeProductosPage() {
  const [productos, setProductos] = React.useState([
    {
      Producto: "Laptop HP EliteBook",
      idProducto: 1,
      nombre: "Laptop HP EliteBook",
      descripcion: "Core i7 16GB RAM 512GB SSD, 14\" FHD",
      precioUnitario: 4899.99,
      precioTotal: 73499.85,
      idStock: 15,
      idTipoProducto: 101,
      idProveedor: 201,
      fechaRegistro: "2025-06-01"
    }
  ])
  const [mostrarModal, setMostrarModal] = React.useState(false)

  const agregarProducto = (nuevoProducto: any) => {
    setProductos([...productos, nuevoProducto])
    setMostrarModal(false)
  }

  const handleEliminar = (id: number) => {
    const confirmar = window.confirm('¿Estás seguro de eliminar este producto?')
    if (confirmar) {
      setProductos(productos.filter(p => p.idProducto !== id))
    }
  }

  const handleEditar = (producto: any) => {
    alert(`Editar producto: ${producto.nombre}`)
    // Aquí podrías abrir otro modal para editar
  }

  return (
    <div className="dashboard-container">
      <HeaderProductos 
        total={productos.length} 
        onNuevoProductoClick={() => setMostrarModal(true)} 
      />
      <FiltrosProductos />
      <TablaProductos 
        productos={productos} 
        onEditar={handleEditar} 
        onEliminar={handleEliminar} 
      />
      {mostrarModal && (
        <FormularioNuevoProductoModal 
          onAgregar={agregarProducto}
          onCerrar={() => setMostrarModal(false)}
        />
      )}
    </div>
  )
}
