'use client'
import React from 'react'
import { FaEdit, FaTrash } from 'react-icons/fa'

interface Producto {
  idProducto: number
  nombre: string
  descripcion: string
  precioUnitario: number
  precioTotal: number
  idStock: number
  idTipoProducto: number
  idProveedor: number
  fechaRegistro: string
}

interface TablaProps {
  productos: Producto[]
  onEditar?: (producto: Producto) => void
  onEliminar?: (id: number) => void
}

export default function TablaProductos({ productos, onEditar, onEliminar }: TablaProps) {
  return (
    <div className="products-table-container">
      <table className="products-table">
        <thead>
          <tr>
            <th className="text-left">Producto</th>
            <th className="text-center">Descripción</th>
            <th className="text-center">Stock</th>
            <th className="text-right">Precio Unitario</th>
            <th className="text-center">Acciones</th>
          </tr>
        </thead>
        <tbody>
          {productos.map((producto) => (
            <tr key={producto.idProducto}>
              <td className="product-info">
                <div className="product-name">{producto.nombre}</div>
              </td>
              <td className="text-center">{producto.descripcion}</td>
              <td className="text-center">
                <span className={`stock-badge ${producto.idStock > 10 ? 'high' : 'low'}`}>
                  {producto.idStock} unidades
                </span>
              </td>
              <td className="text-right">
                {producto.precioUnitario.toLocaleString('es-PE', {
                  style: 'currency',
                  currency: 'PEN'
                })}
              </td>
              <td className="text-center">
                <div className="actions-container">
                  <button
                    className="action-btn edit"
                    title="Editar"
                    onClick={() => onEditar?.(producto)}
                  >
                    <FaEdit />
                  </button>
                  <button
                    className="action-btn delete"
                    title="Eliminar"
                    onClick={() => onEliminar?.(producto.idProducto)}
                  >
                    <FaTrash />
                  </button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}
